/* mixins */

// GENERATE THE BACKGROUND-COLOR ··>
@mixin theme-bg-color($bg-color-theme) {
    background-color : lighten($bg-color-theme, 25%);
    opacity: .7;
}

// GENERATE THE COLOR THEME TEXT (link) ··>
@mixin theme-color($color-theme) {
    color : lighten($color-theme, 5%);
    &:hover,
    &:focus {
        opacity: .9;
    }
}

// GENERATE THE BORDER: COLOR, BORDER AND TYPE ··>
@mixin theme-border-color($brd-color, $brd-size:3.5px, $brd-type:solid) {
    border: $brd-size $brd-type lighten($brd-color, 5%);
    border-radius: 10% / 50%;
    &:hover {
        opacity: .7;
    }
}


