// GENERATE CENTER POSITION THIS BLOCK ··>
@mixin center-block() {
    display: block;
    margin: auto;
}

// GENERATE THE CONTAINER ··>
@mixin container($size, $margin, $padding) {
    @include center-block;
    width: $size;
    margin: $margin;
    padding: $padding;
}

// Keep images from scaling beyond the width of their parents ··>
// > img, a > img {
//  @include img-responsive;
@mixin img-responsive($display: block) {
    @include display($display: block);
    max-width: 100%; // Part 1: Set a maximum relative to the parent
    height: auto; // Part 2: Scale the height according to the width, otherwise you get stretching
}

// TEXT-ALIGN ··>
// values: left ,right, center, start, end *...
// call: @include text-(left)
@mixin text-($align: center) {
    text-align: $align;
}

// DISPLAY ··>
// values: flex, block, *...,
// call: @include display(block)
@mixin display($type: flex) {
    display: $type;
}

// [converter] $parent hack ··>
@mixin bg-variant($parent, $color) {
    #{$parent} {
        background-color: $color;
    }
    a#{$parent}:hover,
    a#{$parent}:focus {
        background-color: darken($color, 10%);
    }
}

// RESPONSE LAYOUT ··>
// values :
// desktop, mobile, mobile-p, tablet, tablet-p
// call :
//    @include respond(desktop)
//    {
//       @include flex-(row);
//    }; -> change the flex-direction
@mixin respond($breakpoint) {
    @if $breakpoint==mobile {
        // here `laptop` is the value of $breakpoint
        // when call laptop, we mean the following piece of code
        @media only screen and (max-width: 800px) {
            @content;
        }
    }
    @if $breakpoint==mobile-p {
        @media only screen and (min-width: 800px) and (max-width: 1000px) {
            @content;
        }
    }
    @if $breakpoint==tablet {
        @media only screen and (min-width: 1000px) and (max-width: 1200px) {
            @content;
        }
    }
    @if $breakpoint==tablet-p {
        @media only screen and (min-width: 1200px) and (max-width: 1400px) {
            @content;
        }
    }
}
// generic-flex ··>
$around: space-around;
$between: space-between;
$evenly: space-evenly;
$center: center;

// flex-direction ··>
// values :
// row, row-reverse, column, column-reverse
// call :
//    @include flex-()
//    @include display(column)
@mixin flex-($direction: row) {
    flex-direction: $direction;
}

// flex-wrap ··>
// values :
// wrap, nowrap, wrap-reverse
// call :
//    @include wrap()
//    @include wrap(wrap-reverse)
@mixin wrap($values: nowrap) {
    flex-wrap: $values;
}

// flex-space ··>
// values :
//    $between,
//    $around,
//    $evenly
// call :
//    @include space-()
//    @include space-($around)
//
@mixin space-($value: $between) {
    align-content: $value;
    justify-content: $value;
}

// flex-align ··>
// values :
// flex-start, flex-end, center, baseline, stretch
// call :
// @include flex-align()
// @include flex-align(flex-end)
@mixin align-($items: center) {
    align-items: $items;
}

// flex-flex-flow ··>
// values :
// row wrap, row-reverse wrap,  row-reverse wrap-reverse,
// column wrap, column-reverse wrap, column wrap-reverse, column-reverse wrap-reverse;
// call :
// @include flex-flow()
// @include flex-flow(column-reverse wrap-reverse)
@mixin flex-flow($values: row wrap) {
    flex-flow: $values;
}

// @include simple-flex(column, wrap-reverse, around, start, flex);
@mixin simple-flex($direction, $wrap, $between) {
    @include display;
    @include flex-($direction);
    @include wrap($wrap);
    @include space-($between)
}

// @include dynamic-flex(column, row, wrap-reverse, $around);
@mixin dynamic-flex($direction-1, $direction-2, $wrap, $between) {
    @include display;
    @include flex-($direction-1);
    @include respond(mobile) {
        @include flex-($direction-2);
    };
    @include respond(mobile-p) {
        @include flex-($direction-1);
    };
    @include respond(tablet) {
        @include flex-($direction-1);
    };
    @include respond(tablet-p) {
        @include flex-($direction-1);
    };
    @include wrap($wrap);
    @include space-($between);
}