/* ·· IMPORT ·· */
@import 'mixins';
@import 'function';

/* dynamic config */
body, header, main, footer, section {max-width: $FULL; width: 100%; margin: 0; padding: 0 }

// generic-text ··>
h1, h2, h3, h4, h5, h6 {@include text-(center); font-family: $FONT-TITLE; width: $FULL; font-weight: 650; letter-spacing: 2px; }
p, b, li, button, a {@include text-(center); font-family: $FONT-TEXT; font-weight: 450; line-height: 1.5; letter-spacing: 1.5px; }
strong {font-weight: bold; letter-spacing: 1.5px; }
a {@include theme-color($PRIMARY_COLOR_2); text-decoration: none; font-family: $LINK-TEXT; font-weight: bold; }
ul {padding: 0; }
li {list-style: none; }

// generic-picture ··>
img {@include center-block; max-height: $FULL; max-width: $FULL; object-fit: cover; }

// generic-margin-base ··>
figure {@include center-block; height: auto; width: $FULL; padding: 0;
    figcaption {@include center-block; @include text-(center); padding: 0;
/*        cite {@include center-block; @include theme-color($PRIMARY_COLOR_2); font-size: 1.1rem; font-family: $SPECIAL-TEXT;
        }*/
    }
}

// icon size and color ··>
i, svg {@include center-block; font-size: $BASE-FONT-SIZE + .5rem; color: $PRIMARY_COLOR_9; }
